<template>
<div class="container-fluid">
  <h3>Create Music Post</h3>
  <form class="md-form">
    <input type="text" id="name" class="form-control" v-model='name'>
    <label for="title">Title</label>
  </form>
  <form class="md-form">   
    <input type="text" id="description" class="form-control" v-model='description'>
    <label for="description">Description</label>
  </form>
  
  <form class="md-form">   
    <input type="text" id="category" class="form-control" v-model='category'>
    <label for="description">Category</label>
  </form>
  
  <form class="md-form">   
    <input type="text" id="album" class="form-control" v-model='album'>
    <label for="description">Album</label>
  </form>
  
  <form class="md-form">   
    <input type="text" id="artist" class="form-control" v-model='artist'>
    <label for="description">Artist</label>
  </form>
  
  <form class="md-form">   
    <input type="text" id="genre" class="form-control" v-model='genre'>
    <label for="description">Genre</label>
  </form>
  
  <form class="md-form">   
    <input type="number" id="rating" class="form-control" v-model='rating'>
    <label for="description">Rating</label>
  </form>
    <h5>Review Text</h5>
    <div>
      <quill-editor style="height:500px;margin-bottom:30px;"
      ref="myTextEditor" v-model="content" ></quill-editor>
    </div>
    
  <button type="button" class="btn btn-default" v-on:click="createBlogPost()">POST</button>
</div>
</template>

<!-- Initialize Quill editor -->
<script>
import { quillEditor } from 'vue-quill-editor' 
  export default {
  
  components: {
    quillEditor
  },
  data () {
      return{
        name: '',
        description: '',
        album: '',
        artist:'',
        genre: '',
        rating: 0,
        content: '',
        date: Date.now,
        category: ''
      }
  },
  methods: {
        createBlogPost: function (){
            preloader.on();
            axios({
              method: 'post',
              url: 'blog_api/api/music',
              baseURL: 'https://trash-server.herokuapp.com/',
              headers: {
                'Content-Type': 'application/json'
              },
              data: {
                name: this.name,
                description: this.description,
                album: this.album,
                artist: this.artist,
                genre: this.genre,
                rating: this.rating,
                content: this.content,
                date: Date.now(),
                category: this.category,
                user: 'Cornbread Dan',
                comments: []
              }
            }).then(function(response){
              console.log(response)
            }).catch(function (error) {
              console.log(error);
            });
            
            preloader.off();
            this.initialize();
            
        },
        initialize: function(){
          this.content = '';
          this.name = '';
          this.description = '';
          this.category = '';
          this.album='';
          this.artist='';
          this.genre='';
          this.rating=0;
        }
    }
  }
</script>

<style scoped>

</style>


