<template>
    <div class="container-fluid main-content content">
        <div class="col-md-3 col-lg-3" style="padding:15px;">
            <h3 class="music-headers">Music Now</h3>
            <p class="secondary-text">A page devoted to covering the hottest tracks as they drop. This
                                        is a strict NO J.COLE zone.<p>
            <h3 class="music-headers">Albums of the Week</h3>
            
            <div class="container-aotw">
                <div class="date-aotw">Week Of: March 10</div>
                <img src="../assets/album-art/semper-femina.jpg" class="img-thumbnail album-art"></img>
                <div class="album-aotw">Semper Femina</div>
                <div class="artist-aotw">Laura Marling</div>
            </div>
            <div class="container-aotw">
                <div class="date-aotw">Week Of: March 3</div>
                <img src="../assets/album-art/last-place.jpg" class="img-thumbnail album-art"></img>
                <div class="album-aotw">Last Place</div>
                <div class="artist-aotw">Grandaddy</div>
            </div>
            
        </div>
        
        <div class="col-md-9 col-lg-6" v-if='!dataLoading'>
        
            <div v-for='post in posts'>
                <div class="music-post card">
                    <div>
                        <div style="float:left;position:relative;left:15px;" class="tertiary-color">{{post.category}}</div>
                        <div style="float:right;position:relative;right:15px;color:#F9A825">
                            <i class="material-icons" v-if="post.rating > 0">star</i>
                            <i class="material-icons" v-if="post.rating > 1">star</i>
                            <i class="material-icons" v-if="post.rating > 2">star</i>
                            <i class="material-icons" v-if="post.rating > 3">star</i>
                            <i class="material-icons" v-if="post.rating > 4">star</i>
                        </div>
                    </div>
                        <div class="card-block" style="padding-top:35px;">
                            <span v-if="post.category=='Album Review'">
                                <h3 style="position:relative;right:10px">{{post.album}}</h3>
                                <p class="card-text secondary-text">{{post.artist}}</p>
                            </span>
                            
                            <span v-if="post.category!='Album Review'">
                                <h3 style="position:relative;right:10px">{{post.name}}</h3>
                                <p class="card-text secondary-text">{{post.description}}</p>
                            </span>
                            
                            <router-link :to="{name:'music-post', params:{postId:post._id}}" class="tertiary-color btn-flat">READ MORE</router-link>
                        </div>
                        <!-- Card footer -->
                        <div class="card-data" style="padding-top:10px;padding-bottom:0;">
                            <ul>
                                <li><i class="fa fa-clock-o"></i> {{post.date | moment}} <span style="margin-left:20px;"><i class="material-icons" style="position:relative;top:3px;font-size:18px;">comment</i> 5</span></li>
                            </ul>
                        </div>
                </div>
            </div>
             
        </div>
        
        <div class="col-md-12 col-lg-3" style="padding:15px;" v-if='!dataLoading'>
            <h3 class="music-headers">Songs of the Year</h3>
            <iframe src="https://embed.spotify.com/?uri=spotify%3Auser%3A1226191915%3Aplaylist%3A78xydTNUOusjhekwXivJdU&theme=white" width="100%" height="380" frameborder="0" allowtransparency="true"></iframe>
        </div>
        
        <div class='col-md-9 secondary-text' v-if="dataLoading">
            <i class="material-icons" style="font-size:96px;position:fixed;top:40%;opacity:0.50">library_music</i>
        </div>
        
    </div>
</template>

<script>
import moment from 'moment'

    export default {
        name: 'music',
        filters: {
            moment: function (date) {
                return moment(date).format('MMMM Do YYYY, h:mm:ss a');
            }
        },
        data () {
            return{
            posts: [],
            dataLoading: false
            }
        },
        methods: {
            getMusicPosts: function (){
                preloader.on();
                this.dataLoading = true;
                axios.get('https://trash-server.herokuapp.com/blog_api/api/music')
                    .then(response => this.posts = response.data)
                    .then(_ =>{                
                        this.dataLoading = false;
                        preloader.off();
                    })
                
            }
        },
        created() {
            this.getMusicPosts()
        }
}
</script>

<style>
    .music-post{
        padding:10px;
    }
    .album-art{
        height:50%;
        width:50%;
    }
    .container-aotw{
        padding-bottom:15px;
    }
    .artist-aotw{
        
    }
    .album-aotw{
        font-weight:bold;
    }
    .date-aotw{
        font-weight:bold;
    }
    .music-headers{
        padding-bottom:5px;
    }
</style>


