<template>
<div class="content">
    <ul class="nav nav-pills nav-fill create-nav">
      <li class="nav-item">
        <a class="nav-link" v-on:click="selectedCreate='Blog'">Blog Post</a>
      </li>
      <li class="nav-item">
        <a class="nav-link" v-on:click="selectedCreate='Music'">Music Review</a>
      </li>
    </ul>
  <div class="container-fluid">
    <createBlogPost v-if="selectedCreate=='Blog'"></createBlogPost>
    <createMusicPost v-if="selectedCreate=='Music'"></createMusicPost>
  </div>
</div>
</template>

<!-- Initialize Quill editor -->
<script>
import CreateBlogPost from '../components/reusable/CreateBlogPost'
import CreateMusicPost from '../components/reusable/CreateMusicPost'

  export default {
  
  components: {
    CreateBlogPost,
    CreateMusicPost
  },
  data () {
      return{
        selectedCreate: 'Blog'
      }
  },
  methods: {
        
    }
  }
</script>

<style scoped>
.create-nav{
  padding:15px;
  padding-top:0;
  border-bottom:1px solid #e0e0e0;
  margin-bottom:20px;
  background-color:white;
  color:#0275d8;
  position:relative;
  bottom:15px;
}
.nav-item{
  position:relative;
  top:10px;
}
</style>


