<template>
    <div class="container">
        <div class="row">
            <div class="blog-post">
                <div class="other-content second-color" style="float:left;">{{post.category}}</div>
                <div class="header" style="margin-top:35px;">{{post.name}}<div>
                <div class="other-content" style="border-bottom:1px solid #e0e0e0;opacity:0.72;padding-bottom:10px;">
                <span style="margin-right:3px;"><i class="material-icons" style="position:relative;top:5px;">perm_identity</i>{{post.user}}</span>
                    <div><i class="fa fa-clock-o"></i> {{post.date | moment}}</div></div>
                
                <div class="content" style="text-align:left" v-html="post.content"></div>
            </div>
        </div>
        <div class="card row" style="padding:10px;">
            <div class="col-md-8 offset-md-2">
                <h5>Comments</h5>
                <table class="table">
                    <tr v-for='comment in post.comments'>
                        <th><span class="second-color"><i class="material-icons" style="position:relative;top:5px;">account_circle</i> {{comment.username}}</span></th>
                        <td><p style="position:relative;top:5px;">{{comment.comment}}</p></td>
                    </tr>
                    
                    <tr v-if="newComment.username">
                        <th><span class="second-color" style="position:relative;top:15px;"><i class="material-icons" style="position:relative;top:5px;">account_circle</i> {{newComment.username}} </span></th>
                        <td>
                            <div class="input-group">
                                <input type="text" v-model="newComment.comment" class="form-control" placeholder="Type comment..." aria-describedby="basic-addon1">
                            </div>
                        </td>
                        <td> <button type="button" class="btn btn-cyan" v-on:click="postComment()"><i class="material-icons">send</i></button> </td>
                    </tr>
                    
                    <tr v-if="!newComment.username">
                        <th style="position:relative;top:5px;">
                            <div>Username: </div>
                        </th>
                        <td>
                            <div class="input-group">
                                <input type="text" v-model="username" class="form-control" placeholder="Enter username..." aria-describedby="basic-addon1">
                            </div>
                        </td>
                        <td>
                            <button type="button" class="btn btn-cyan" v-on:click="createAccount(username)">Start Posting</button>
                        </td>
                    </tr>
                </table>
                
            </div>
        </div>
    </div>
</template>

<script>
import moment from 'moment'
    export default {
        name: 'musicpost',
        data () {
            return{
                post: {},
                newComment: {
                    username: localStorage.getItem('currentUser'),
                    comment: ''
                },
                username: ''
            }
        },
        filters: {
            moment: function (date) {
                return moment(date).format('MMMM Do YYYY, h:mm a');
            }
        },
        methods: {
            getMusicPostById: function (){
                preloader.on();
                axios.get('https://trash-server.herokuapp.com/blog_api/api/music/' + this.$route.params.postId)
                    .then(response => this.post = response.data)
                    .then(function(){
                        preloader.off();
                    })
                
            },
            postComment: function (){
                this.post.comments.push(this.newComment)
                            axios({
                                method: 'put',
                                url: 'blog_api/api/music/' + this.$route.params.postId,
                                baseURL: 'https://trash-server.herokuapp.com/',
                                headers: {
                                    'Content-Type': 'application/json'
                                },
                                data: this.post
                                }).then(_ =>{
                                    console.log('refreshing')
                                    this.getBlogPostById();
                                    this.newComment.comment = '';  
                                })
                                
            },
            createAccount: function(username){
                this.newComment.username = username
                localStorage.setItem('currentUser', username)
            }
        },
        created() {
            this.getMusicPostById()
        }
     }
     

</script>

<style scoped>
    .blog-post{
        min-height:500px;
        padding:20px;
    }
    .header{
        font-size:30px;
    }
    .sub-header{
        font-size:20px;
    }
    .other-content{
        font-size:16px;
    }
    .content{
        font-size:16px;
        padding:20px;
    }
</style>


